import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapMutations } = createNamespacedHelpers('Chat')

export default {
  name: 'RoomRecordSelect',
  props: {
    isVisible: {
      type: Boolean
    },
    modules: {
      type: Array
    }
  },
  data() {
    return {
      selectModule: null,
      searchModules: []
    }
  },
  watch: {
    isVisible(val) {
      if (val) {
        setTimeout(() => {
          this.$refs.selectModule.showPopup()
        }, 100)
      } else {
        this.selectModule = null
        this.$refs.selectModule.hidePopup()
      }
    }
  },
  computed: {
    ...mapGetters(['config'])
  },
  methods: {
    ...mapMutations(['updateRooms']),
    filter(val, update) {
      if (val === '') {
        update(() => {
          this.searchModules = this.modules
        })
        return
      }
      update(() => {
        const needle = val.toLowerCase()
        this.searchModules = this.modules.filter(
          v => v.label.toLowerCase().indexOf(needle) > -1
        )
      })
    },
    showRecordsModal(val) {
      if (!val) {
        this.$refs.selectModule.showPopup()
        return
      }
      app.showRecordsList(
        { module: val, src_module: val },
        (modal, instance) => {
          instance.setSelectEvent((responseData, e) => {
            AppConnector.request({
              module: 'Chat',
              action: 'Room',
              mode: 'addToFavorites',
              roomType: 'crm',
              recordId: responseData.id
            }).done(({ result }) => {
              this.updateRooms(result)
            })
          })
        }
      )
    }
  },
  created() {
    this.searchModules = this.modules
  }
}
